<template>
  <div class="box">
    <indexHeader></indexHeader>
    <div class="account">
      <div class="topcontent ft20">
        <span class="white">
          <span class="baseColor">{{$t('asset.assets')}}></span>
         绑定提币地址
        </span>
      </div>
      <div class="content">
        <div class="con_box">
          <div class="contentBK fColor1 pdlr20 pdtb20 ft12">
            <div class="flex alcenter">
              <span class="w10 mr20">{{$t('asset.currency')}}</span>
              <span class="flex1 mr20">{{$t('asset.address_width')}}</span>
              <span class="flex1">验证码</span>
            </div>
            <div class="mt10">
              <div class="flex alcenter">
                <select name id class="w10 mr20" v-model="addId" @change="setCoin">
                  <!-- <option :value="this.$t('asset.currency')">{{$t('asset.currency')}}</option> -->
                  <option
                    :value="item.currency_name"
                    v-for="(item,index) in coins"
                    :key="index"
                  >{{item.currency_name}}</option>
                </select>
                <input
                  type="text"
                  class="flex1 mr20"
                  :placeholder="this.$t('asset.enterAdderses')"
                  v-model="address"
                />
                <div class="flex1 flex alcenter">
                  <input type="text" style="flex:1" class placeholder="请输入验证码" v-model="notes" />
                  <button style="flex:0.8" class="btn curPer" @click="getCode" :disabled="disable">{{codeText}}</button>
                </div>
              </div>
              <div class="add">
                <div class="baseBtn" @click="addAddress">绑定提币地址</div>
              </div>
            </div>
          </div>
        </div>
        <!-- <div class="con_box mt15">
          <div class="account_title ttBk white flex">
            <div class="ml20">{{$t('asset.addressList')}}</div>
          </div>
          <div class="contentBK fColor1 pdlr20 pdtb20 ft12">
            <div class="flex alcenter">
              <span class="flex1">{{$t('asset.currency')}}</span>
              <span class="flex1">{{$t('asset.address_width')}}</span>
              <span class="flex1">{{$t('asset.beizhu')}}</span>
              <span class="flex1">{{$t('td.do')}}</span>
            </div>
            <div class="mt10" v-for="(item,index) in list" :key="index">
              <div class="flex alcenter">
                <span class="flex1">{{nowCoin}}</span>
                <span class="flex1">{{item.address}}</span>
                <span class="flex1">{{item.notes}}</span>
                <span class="flex1" @click="delAddress(item.id,index)">{{$t('asset.delete')}}</span>
              </div>
            </div>
            <div
              class="none color1 tc"
              v-if="!list.length"
              style="padding: 20px 0"
            >{{$t('asset.norec')}}</div>
          </div>
        </div> -->
      </div>
    </div>
  </div>
</template>
<script>
import indexHeader from "@/view/indexHeader";
export default {
  name: "finanrec",
  data() {
    return {
      token: "",
      lang: "",
      coins: [],
      nowCoin: "",
      list: [],
      address: "",
      notes: "",
      addId: '',
      codeText: "获取验证码",
      disable:false
    };
  },
  components: {
    indexHeader
  },
  created() {
    this.getCoins();
    console.log(this.API)
  },
  methods: {
    getCoins() {
      this.token = localStorage.getItem("token") || "";

      this.$http({
        url: "/api/wallet/list",
        method: "POST",
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok" && res.data.message.length != 0) {
          this.coins = res.data.message.legal_wallet.balance;
           this.addId = this.coins[0].currency_name;
        };
        this.getAddress();
      });
    },
    //获取验证码
    // /walletMiddle/SendVerificationcodev
    getCode() {
      var that = this;
      this.$http({
        url: this.API+"/walletMiddle/SendVerificationcode",
        method: "GET",
        params:{
          user_id:localStorage.getItem("user_id")
        }
      }).then(res => {
        if (res.data.code == 0) {
          layer.msg("发送成功");
          var times = 30
          var timer = setInterval(() => {
            times--;
            that.codeText = times + "s";
            that.disable = true;
            if (times <= 0) {
              clearInterval(timer);
              that.codeText = '获取验证码';
              that.disable = false;
            }
          }, 1000);
        } else {
          layer.msg(res.data.msg);
        }
      });
    },
    setCoin(e) {
      // if (this.addId != this.$t("asset.currency")) {
      //   let index = e.target.selectedIndex;
      //   let opt = e.target.querySelectorAll("option")[index];
      //   this.nowCoin = opt.text;
      //   this.getList();
      // }
      this.getAddress();
      this.address = ''
    },
    //获取提币地址
    getAddress(){
      var that = this;
      this.$http({
        url: this.API+"/walletMiddle/GetDrawAddress",
        method: "GET",
        params:{
          user_id:localStorage.getItem('user_id'),
          coin_name:that.addId,
          contract_addres:''
        }
      }).then(res => {
        if (res.data.code == 0) {
          that.address = res.data.data.address
        } else {
          
        }
      });
    },
    getList() {
      this.token = localStorage.getItem("token") || "";

      this.$http({
        url: "/api/wallet/get_address",
        method: "post",
        data: { currency: this.addId },
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok") {
          this.list = res.data.message;
        } else {
          this.list = [];
        }
      });
    },
    delAddress(id, index) {
      this.$http({
        url: "/api/wallet/deladdress",
        method: "post",
        data: { address_id: id },
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok") {
          this.list.splice(index, 1);
          layer.msg(res.data.message);
        }
      });
    },
    //绑定地址
    addAddress(){
      var that = this;
      if (this.address == "") {
        layer.msg(this.$t("asset.enterAdderses"));
        return;
      };
      if (this.notes == "") {
        layer.msg('请输入验证码');
        return;
      };
      var obj = {
                user_id:localStorage.getItem('user_id'),
                address:this.address,
                coin_name:this.addId,
                contract_address:'',
                verificationcode:this.notes,
                t:Date.parse(new Date())/1000,
            };
        var obj_str = JSON.stringify(obj);
        console.log(obj_str);
        console.log($.md5(obj_str+'abcd4321'))
        $.ajax({
            url: this.API+'/walletMiddle/BindDrawAddress',
            type: "POST",
            dataType: "json",
            async: true,
            data:{
                data:obj_str,
                sign: $.md5(obj_str+'abcd4321')
            },
            success: function (res) {
                if(res.code == 0){
                   layer.msg('绑定成功');
                   that.address = '';
                   that.notes = '';
                }else{
                    layer.msg(res.msg)
                }
            }
        });
    }
    // addAddress() {
    //   if (this.addId == this.$t("asset.currency")) {
    //     layer.msg(this.$t("asset.selectCurrency"));
    //     return;
    //   }
    //   if (this.address == "") {
    //     layer.msg(this.$t("asset.enterAdderses"));
    //     return;
    //   } else {
    //     this.$http({
    //       url: "/api/wallet/addaddress",
    //       method: "post",
    //       headers: { Authorization: this.token },
    //       data: {
    //         currency_id: this.addId,
    //         address: this.address,
    //         notes: this.notes
    //       }
    //     }).then(res => {
    //       if (res.data.type == "ok") {
    //         layer.msg(res.data.message);
    //         this.address = "";
    //         this.notes = "";
    //         this.getList();
    //       }
    //     });
    //   }
    // }
  }
};
</script>

<style lang="scss" scoped>
.box .account {
  width: 1200px;
  margin: 0 auto 82px;
  margin-top: 30px;
}
.topcontent {
  padding: 22px 30px;
  margin-bottom: 20px;
  background-color: #1a2330;
  border-radius: 5px;
}
.account_title {
  padding: 15px 20px;
}
select {
  background-color: transparent;
  padding: 10px;
  color: #c7cce6;
  font-size: 18px;
  border-radius: 3px;
  border: 1px solid #4e5b85;
}
select.ft14 {
  font-size: 14px;
}
select > option {
  padding: 10px 0;
  background-color: transparent;
}
input {
  padding: 10px;
  border: 1px solid #4e5b85;
  font-size: 18px;
  color: #c7cce6;
  border-radius: 3px;
  background-color: transparent;
  outline: none;
}
.add {
  text-align: right;
  margin-top: 20px;
}
.add .baseBtn {
  width: 200px;
}
.btn {
  width: 150px;
  height: 100%;
  line-height: 42px;
  border-radius: 3px;
  background: #5697f4;
  color: #fff;
  margin-left: 20px;
}
</style>





